@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'OCR-B';
  src: url('assets/OcrB2.ttf') format('truetype');
  /* Add additional src formats as needed for cross-browser compatibility */
}

body {
  margin: 0;
  font-family: 'OCR-B', monospace;
  font-weight:100;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#F1F0EA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.my-container{
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
}

.my-card{
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

.my-container::-webkit-scrollbar {
  display: none;
}